<template>
  <div>
    <div class="page-title">
      <h3>Личная статистика игрока - {{$route.params.nick}}</h3>

      <button class="btn waves-effect waves-light btn-small" @click="refresh">
        <i class="material-icons">refresh</i>
      </button>
    </div>
    <div class="row">
      <Loader v-if="loading"/>
      <div v-else>
            <p>Общая статистика:</p>
        <table>
        <tr>
         <th>Ник</th>
          <th>Кол-во игр</th>
          <th>Убийств</th>
          <th>Тимкиллов</th>
          <th>Смертей</th>
          <th>K/D</th>
          <th>Счет</th>
          </tr>
        <tr v-for="(i, index) of n" :key="index">
          <td><router-link :to="{name:'Player', params:{nick: n[index].playerName, stats: i}}">{{n[index].lastSquadPrefix + n[index].playerName}}</router-link></td>
          <td>{{n[index].totalPlayedGames}}</td>
          <td>{{n[index].kills}}</td>
          <td>{{n[index].teamkills}}</td>
          <td>{{n[index].deaths}}</td>
          <td>{{n[index].kdRatio}}</td>
          <td>{{n[index].totalScore}}</td>
        </tr>
      </table>
      <p>Статистка по неделям:</p>
        <table>
        <tr>
         <th>Даты</th>
          <th>Кол-во игр</th>
          <th>Убийств</th>
          <th>Тимкиллов</th>
          <th>Смертей</th>
          <th>K/D</th>
          <th>Счет</th>
          </tr>
        <tr v-for="(i, index) of byWeeks" :key="index">
          <td>{{byWeeks[index].startDate}} - {{byWeeks[index].endDate}}</td>
          <td>{{byWeeks[index].totalPlayedGames}}</td>
          <td>{{byWeeks[index].kills}}</td>
          <td>{{byWeeks[index].teamkills}}</td>
          <td>{{byWeeks[index].deaths}}</td>  
          <td>{{byWeeks[index].kdRatio}}</td>
          <td>{{byWeeks[index].totalScore}}</td>
        </tr>
      </table>

      </div>

    </div>
  </div>
</template>

<script>
export default {
  name: "Player",
  data: () => ({
    n: [],
    byWeeks: [],
    loading: false,
  }),
  async mounted() {
      this.loading = true;
      await this.r();
      this.loading = false;
  },
  methods: {
    async refresh() {
      this.loading = true;
        this.n = [];
      await this.r()
      this.loading = false;
    },
    async r(){
    let r = await fetch("../stats.json");
    r = await r.json();
      for (let key in r.globalStatistics) {
        if (r.globalStatistics[key].playerName === this.$route.params.nick) {
          this.n.push(r.globalStatistics[key]);
          this.byWeeks = r.globalStatistics[key].byWeeks;
        }
      }
      for(let key in this.byWeeks){
          this.byWeeks[key].startDate = new Date(this.byWeeks[key].startDate).toLocaleString('ru', {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      });
        this.byWeeks[key].endDate = new Date(this.byWeeks[key].endDate).toLocaleString('ru', {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      });
      }
    }
  },
};
</script>
